    -moz-transition: all 0.9s 2s ease, all 0.5s 0s ease;
    -o-transition: all 0.9s 2s ease, all 0.5s 0s ease;
    transition: all 0.9s 2s ease, all 0.5s 0s ease;
    min-height: 250px;
}

@media (min-width: 900px) {
    .dropdown:hover .dropdown-menu {
        display: block;
        margin-top: 0;
